@media (max-width: 1200px) {
	.menu {
		&_item {
			padding-right: 20px; }
		&_link {
			font-size: 13px; } }
	.promo {
		&_header {
			font-size: 50px; } }
	.reasons {
		&_block {
			align-items: flex-start; }
		&_descr {
			width: 300px; } }
	.mobile {
		&_item {
			width: 300px; } } }

@media (max-width: 991px) {
	.menu {
		&_item {
			padding-right: 8px; }
		&_link {
			font-size: 10px; } }
	.subheader {
		&_logo {
			img {
				width: 100%; } }
		&_official {
			font-size: 11px; }
		&_phone {
			font-size: 18px; }
		&_btn {
			width: 100%;
			height: 40px;
			margin-top: 21px; } }
	.promo {
		&_header {
			font-size: 40px; }
		&_subheader {
			font-size: 31px; } }
	.reasons {
		&_block {
			align-items: center;
			flex-direction: column; }
		&_round {
			margin-right: 0; }
		&_descr {
			margin-top: 20px;
			width: 300px; } }
	.mobile {
		&_item {
			width: 220px;
			&_subtitle {
				font-size: 23px; } } }
	.choice {
			&_img {
				width: 100%; } }
	.footer {
		&_socialicons {
			position: relative;
			bottom: 0;
			left: 0;
			display: flex;
			justify-content: center;
			margin: 0px 0 30px 0; }
		&_stores {
			position: relative;
			display: flex;
			justify-content: center;
			margin-bottom: 65px;
			right: 0; }
		&_block {
			height: 145px; } } }

@media (max-width: 767px) {
	.menu {
		position: fixed;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		top: 0;
		left: -100%;
		width: 50%;
		height: 100vh;
		padding: 20px 15px;
		background-color: rgba(0, 0, 0, .8);
		transition: 0.6s all;
		&_active {
			left: 0; }
		&_item {
			padding-right: 0;
			border: none;
			margin: 12px 0; }
		&_link {
			font-size: 20px; } }
	.hamburger {
		display: block;
		position: absolute;
		right: 10px;
		top: 40%;
		transform: translate(-50%);
		width: 26px;
		height: 12px;
		span {
			display: block;
			height: 2px;
			width: 100%;
			background-color: #fff;
			margin-bottom: 2px;
			transition: 0.5s all; }
		&_active {
			span {
				margin-bottom: -2px;
				&:nth-child(1) {
					transform: translateY(3px) rotate(-45deg); }
				&:nth-child(2) {
					display: none; }
				&:nth-child(3) {
					transform: translateY(3px) rotate(45deg); } } } }
	.xs-hidden {
		display: none; }
	.xs-visible {
		display: block; }
	.subheader {
		&_phone {
			display: block;
			margin-top: 20px;
			font-weight: 400;
			font-size: 15px;
			text-transform: uppercase;
			color: #FFFFFF;
			text-align: center; }
		&_btn {
			display: block;
			width: 180px;
			height: 30px;
			margin: 0 auto;
			margin-top: 10px; } }
	.promo {
		&_header {
			// font-size: 40px
			line-height: 45px; } }
	.reasons {
		&_subtitle {
			text-align: center;
			font-size: 19px; }
		&_text {
			text-align: center;
			font-size: 13px;
			margin-top: 10px; } }
	.mobile {
		&_item {
			width: 520px;
			height: 240px; } }
	.choice {
		padding-bottom: 0;
		min-height: 760px; }
	.map {
		min-height: 440px;
		height: 440px;
		&_descr {
			margin-top: 205px; } }

	.footer {
		&_h1 {
			justify-content: space-evenly; } } }

@media (max-width: 575px) {
	nav {
		height: 30px; }
	header {
		height: 30px; }
	.subheader {
		height: 60px;
		top: 30px;
		&_official {
			display: none; }
		&_logo {
			margin-top: 16px; }
		&_btn {
			width: 110px;
			height: 20px;
			font-size: 10px;
			margin-top: 6px;
			&:hover {
				background-image: linear-gradient(to top, $main-color 0%, #4ce2ff 100%); } }
		&_phone {
			margin-top: 11px;
			font-weight: 400;
			font-size: 12px;
			text-transform: uppercase; } }
	.promo {
		min-height: 400px;
		padding: 93px 0 35px 0;
		&_descr {
			display: none; }
		&_header {
			font-size: 30px;
			padding: 0 24px;
			line-height: 35px; }
		&_subheader {
			font-size: 20px;
			margin-top: 32px;
			padding: 0 10px;
			line-height: 30px; }
		&_btn {
			width: 210px;
			height: 50px;
			margin-top: 60px;
			&:hover {
				background-image: linear-gradient(to top, $main-color 0%, #4ce2ff 100%); } } }

	.menu {
		width: 85%;
		&_item {
			width: 100%;
			text-align: center;
			padding: 5px 0; }
		&_link {
			display: block; } }

	.reasons {
		padding: 22px 0 36px 0;
		&_block {
			margin-top: 26px; }
		&_round {
			width: 100px;
			height: 100px;
			img {
				width: none; } }
		&_descr {
			width: 100%;
			margin-top: 27px; }
		&_subtitle {
			font-size: 16px; } }
	.title {
		font-size: 15px;
		margin-top: 15px;
		line-height: 16px; }

	.mobile {
		&_item {
			width: 290px;
			height: 240px; } }
	.choice {
		min-height: 488px; }

	.footer {
		&_wrap {}
		display: block;
		padding-right: none;
		padding-left: none;
		&_text {
			display: block;
			width: 100%;
			margin-left: 15px;
			a {
				padding-right: 24px;
				margin-top: 5px; } }
		&_h1 {
			font-size: 13px;
			margin-top: 21px; }
		&_block {
			height: 120px; }
		&_line {
			// margin: 0
 }			// margin-top: 24px
		&_socialicons {
			margin: 14px 0 19px 0; }
		&_stores {
			margin-bottom: 10px; } } }

